<template>
    <div class="mockups">
      <div class="search-container mb-3">
        <h1>Assets you need</h1>
        <p>Find and download the best illustrations, designs, and mockups</p>
        <b-form>
            <b-form-group>
                <b-input-group>
                    <b-form-input size="lg" type="search" v-model="keywords" placeholder="Search..."></b-form-input>
                    <b-button>
                        <b-icon icon="search"></b-icon>
                    </b-button>
                </b-input-group>
            </b-form-group>
        </b-form>
      </div>
      <b-card-group >
        <b-card v-for="(mockup, index) in mockups" :key="index" title=""
           img-alt="Mockup" img-top>
           <img :src="require(`../assets/mockups/${mockup.name}`)" :alt="mockup.name"/>
          <div class="text-right mt-3">
            <a  :href="mockup.name" :download="mockup.name">Download</a>
          </div>
        </b-card>
      </b-card-group>
    </div>
</template>
<script>
import images from "../assets/paths.json";
export default{
    data:()=>({
        list:null,
        keywords:"",
    }),
    
    computed: {
        mockups() {
            return this.list.filter(img => {
                return img.name.toLowerCase().includes(this.keywords);
            });
        }
    },
    created(){
       this.list = images;
    }
}
</script>
